.hero {
  height: 500px;
  background-size: cover;
  background-position: center;
  position: relative;
  border-radius: 5px;
  background-color: white;
  border: black solid 3px;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;

}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
  font-family: Plumee, Helvetica, Arial, sans-serif;
}

.hero h1 {
  font-size: 105px;
  padding-top: 140px;
  margin-top: 0;
  font-weight: bolder;
}


@font-face {
font-family: Plumee;
src: url(Hero_Font1.ttf);
}



